			<el-header>
				<div style="background-color: #373d41">
					<img src="../image/logo.png" style="height: 55px; width: 140px;" />
				</div>
				<span class="font">重大传染病流调系统</span>
				<div class="firework">
					<lottie-player src="https://assets3.lottiefiles.com/packages/lf20_fJ7CVd.json"
						background="transparent" speed="1" style="width: 200px; height: 60px;" loop autoplay>
					</lottie-player>
				</div>
				<el-button type="primary" round class="exit" @click="exit()">退 出</el-button>
			</el-header>

			<el-container style="padding: 0;">
				<el-aside :width="isCollapse ? '64px': '200px'">
					<div class="toggle">|||</div>
					<el-menu :collapse="isCollapse" :collapse-transition="false" :router="true"
						background-color="#333744" text-color="#fff" active-text-color="#409EFF">
						<!-- 首页导航 -->
						<el-menu-item index="/home">
							<i class="el-icon-house"></i>
							<span>首页</span>
						</el-menu-item>
						<!-- 一级导航 -->
						<el-submenu index="1">
							<template slot="title">
								<i class="el-icon-s-custom"></i>
								<span>用户</span>
							</template>
							<!-- 二级导航 -->
							<el-menu-item-group>
								<el-menu-item index="/person"><i class="el-icon-user"></i>个人信息</el-menu-item>
								<el-menu-item index="/record"><i class="el-icon-document"></i>我的记录</el-menu-item>
							</el-menu-item-group>
						</el-submenu>
						<!-- 一级导航 -->
						<el-submenu index="2">
							<template slot="title">
								<i class="el-icon-search"></i>
								<span>查询</span>
							</template>
							<!-- 二级导航 -->
							<el-menu-item-group>
								<el-menu-item index="/search"><i class="el-icon-search"></i>查询患者</el-menu-item>
								<el-menu-item index="/back"><i class="el-icon-refresh"></i>查询回溯</el-menu-item>
								<el-menu-item index="/enter"><i class="el-icon-edit-outline"></i>人工录入</el-menu-item>
							</el-menu-item-group>
						</el-submenu>
						<!-- 一级导航 -->
						<el-submenu index="3">
							<template slot="title">
								<i class="el-icon-setting"></i>
								<span>设置</span>
							</template>
							<!-- 二级导航 -->
							<el-menu-item-group>
								<el-menu-item index="/background"><i class="el-icon-sort"></i>切换背景</el-menu-item>
								<el-menu-item index="/manage"><i class="el-icon-wallet"></i>账号管理</el-menu-item>
								<el-menu-item index="/check"><i class="el-icon-setting"></i>检查更新</el-menu-item>
								<el-menu-item index="/about"><i class="el-icon-info"></i>关于我们</el-menu-item>
							</el-menu-item-group>
						</el-submenu>
					</el-menu>
				</el-aside>

				<el-main :style="defaultHeight">
					<div style="overflow-y:auto;height:100%;">
						<router-view></router-view>
					</div>
				</el-main>

			</el-container>
		</el-container>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				videoCanPlay: false,
				isCollapse: false,
				defaultHeight: {
					height: ""
				}
			}
		},
		methods: {
			/* author:孙玉斐
			date:7.9
			页面侧边栏折叠函数*/
			toggleCollapse() {
				this.isCollapse = !this.isCollapse
			},

			/* author:孙玉斐
			date:7.9
			退出主界面并跳转到登录界面*/
			exit() {
				this.$confirm('是否确认要退出?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$router.push('./login');
				});
			},

			getHeight() {
				this.defaultHeight.height = window.innerHeight - 62 + "px";
			}

		},
		created() {
			//页面创建时执行一次getHeight进行赋值，顺道绑定resize事件
			window.addEventListener("resize", this.getHeight);
			this.getHeight();
		}
	}
</script>
<style>
	.home {
		height: 100%;
		width: 100%;
	}

	.el-header {
		background-color: #373d41;
		color: #333;
		height: 60px;
		padding-left: 0;
	}

	.el-aside {
		background-color: #333744;

	}

	.el-main {
		background-color: #E9EEF3;
		text-align: center;
		padding: 0;
		margin: 0;
		z-index: 1;
	}

	.main {
		z-index: -1;
		position: absolute;
		width: 89.46%;
		height: 93.5%;
		margin: 0;
		padding: 0;
	}

	.el-main video {
		z-index: -1;
		object-fit: fill;
		height: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	.el-aside .el-menu {
		border-right: none;
	}

	.el-header .exit {
		width: 120px;
		position: absolute;
