		font-size: 20px;
		position: absolute;
		left: 140px;
		top: 15px;
		color: #FFFFFF;
	}

	.toggle {
		background-color: #4a5064;
		font-size: 10px;
		line-height: 24px;
		color: #666666;
		text-align: center;
		letter-spacing: 0.3em;
		cursor: pointer;
	}
</style>
