<template onload="initMap()">
	<!-- 定义地图显示容器 -->
	<div class="box">
		<div id="container"></div>
		<div class="info">
			<el-button type="primary" round class="button" @click="getOtherLocation()">回 溯</el-button>
		</div>
		<div class="word1">
			<div class="line1"></div>
			<span style="color: white;">红色的线表示患者本人路径</span>
		</div>
		<div class="word2">
			<div class="line2"></div>
			<span style="color: white;">绿色的线表示与患者有接触人员路径</span>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			name: 'Map'
			return {
				judge: true,
				marked: [], //存储服务器得来的经纬度数据
				path: [], //存储路径各点坐标
				tril: [], //临时变量来存储坐标最后赋值给path
				name: [], //存储姓名信息
				identy: [], //存储身份证信息
				Map, //地图图层
				markerLayer: "", //查询的人标记图层
				polylineLayer: "", //查询的人折线图层
				othermarkerLayer: "", //其他人的标记图层
				otherpolylineLayer: "", //其他人的折线图层
				temp: [],
			}
		},
		methods: {
			/*author:李浩杨
			  data:2021/7/16
			  实现地图上标记与患者相交路线的路径与区域*/
			getOtherLocation() {
				this.$confirm('是否确认要进行回溯?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					let that = this
					that.othermarkerLayer.setVisible(true)
					that.otherpolylineLayer.setVisible(true)
					if (that.judge) {
						that.judge = !that.judge
						this.$axios
							.post('https://120.25.76.71/api/func', {
								"name": "四号",
								"identy": "123456200001010104",
								"time": ["2021-07-16 00:00:00", "2021-07-19 00:00:00"]
							})
							.then(res => {
								for (var i = 2; i < res.data.all_routes.length; i += 3) {
									for (var j = 0; j < res.data.all_routes[i].length; j++) {
										var arr = {
											firlat: res.data.all_routes[i]. [j]. ["0"],
											firlng: res.data.all_routes[i]. [j]. ["1"],
											firTime: res.data.all_routes[i]. [j]. ["2"],
											laslat: res.data.all_routes[i]. [j]. ["3"],
											laslng: res.data.all_routes[i]. [j]. ["4"],
											lasTime: res.data.all_routes[i]. [j]. ["5"],
										}
										let oddlist = that.marked[res.data.all_routes. [i - 2]] || []
										oddlist.push(arr)
										that.marked[res.data.all_routes. [i - 2]] = oddlist
									}
									that.name.push(res.data.all_routes. [i - 2])
									that.identy.push(res.data.all_routes. [i - 1])
								}
								console.log(that.identy)
								console.log(that.marked)
								console.log(that.marked[that.name[0]])
								// console.log(that.marked[that.name[0]]. [0].firTime)
								// console.log(that.marked[that.name[0]]. [(that.name[0].length - 1)].lasTime)
								//将获取过来的数组进行for循环来为标记图层加标记
								for (var i = 0; i < that.name.length; i++) {
									var num = 0;
									for (var j = 0; j < that.marked[that.name[i]].length; j++) {
										that.othermarkerLayer.add({
											"id": that.name[i] + num,
											// "styleId":'otherStyle',
											"position": new TMap.LatLng(that.marked[that.name[i]]. [j]
												.firlat, that
												.marked[that.name[i]]. [j].firlng),
											//如果要在信息框显示人的名字，可以在这里加入自定义的属性讲name存入
											"properties": {
												"name": that.name[i],
												"Time": that.marked[that.name[i]]. [j].firTime,
												"identy": that.identy[i]
											}
										})
										num += 1
										that.othermarkerLayer.add({
											"id": that.name[i] + num,
											// "styleId":'otherStyle',
											"position": new TMap.LatLng(that.marked[that.name[i]]. [j]
												.laslat, that
												.marked[that.name[i]]. [j].laslng),
											"properties": {
												"name": that.name[i],
												"Time": that.marked[that.name[i]]. [j].lasTime,
												"identy": that.identy[i]
											}
										})
										num += 1
									}
								}
								//加折线
								for (var i = 0; i < that.name.length; i++) {
									var otherPolyArray = new Array();
									var z = 0
									for (var j = 0; j < that.marked[that.name[i]].length; j++) {
										otherPolyArray[z] = [that.marked[that.name[i]]. [j].firlat, that
											.marked[that.name[i]].
											[j].firlng
										]
										z += 1;
										otherPolyArray[z] = [that.marked[that.name[i]]. [j].laslat, that
											.marked[that.name[i]].
											[j].laslng
										]
										z += 1;
									}
									console.log(otherPolyArray)
									otherPolyArray = otherPolyArray.map(p => {
										return new TMap.LatLng(p[0], p[1]);
									})
									console.log(otherPolyArray)
									that.otherpolylineLayer.add({ //新的折线添加到图层中
										'id': that.name[i],
										'styleId': 'style_green',
										'paths': otherPolyArray
									})
								}
								//加轨迹
								//for(var i=0;i<that.name.length;i++){
								//  var otherpath = new Array()	
								// }
							});
					} else {
						that.othermarkerLayer.setVisible(false)
						that.otherpolylineLayer.setVisible(false)
						console.log("隐藏")
						that.judge = !that.judge
					}
				}).catch(() => {});
			},

			/*author:李浩杨、孙玉斐
			  data:2021/7/16
			  在页面显示地图并实现地图上标记和点标记显示信息窗口的功能和存储从服务器获得的值*/
			initMap(x, y, z) {
				// 定义地图中心点坐标
				var center = new window.TMap.LatLng(39.792151, 116.397607)
				// 定义map变量，调用 TMap.Map() 构造函数创建地图
				this.Map = new window.TMap.Map(document.getElementById('container'), {
					center: center, // 设置地图中心点坐标
					zoom: 15, // 设置地图缩放级别
					pitch: 43.5, // 设置俯仰角
					rotation: 45, // 设置地图旋转角度
					mapStyleId: 'style1',
					baseMap: { //底图设置
						type: 'vector', //类型：失量底图
						features: ['base', 'building2d']
						//仅渲染：道路及底面(base) + 2d建筑物(building2d)，以达到隐藏文字的效果
					}
				})
				/*author:李浩杨
				  data:2021/7/12
				  实现地图上标记和点标记显示信息窗口的功能和存储从服务器获得的值*/
				let that = this;
				// that.temp["name"] = this.$route.query.name;
				// that.temp["identy"] = this.$route.query.identy;
				// that.temp["time"] = this.$route.query.time;
				this.$axios
					.post('https://120.25.76.71/api/func', {
						"name": x,
						"identy": y,
						"time": z
					})
					.then(res => {
						console.log(res.data);
						console.log(res.data.routes.length)
						for (var i = 0; i < res.data.routes.length; i++) {
							var array = {
								lat: res.data.routes[i]. ["1"],
								lng: res.data.routes[i]. ["2"],
								time: res.data.routes[i]. ["0"]
							};
							let oddlist = that.marked['location'] || []
							oddlist.push(array)
							that.marked['location'] = oddlist
							that.tril.push(array)
						}
						var j = 0
						for (var item in that.tril) {
							that.path[j] = [that.tril[j].lat, that.tril[j].lng],
								j = j + 1
						}
						//将地址定位到第一个点
						that.Map.setCenter(new TMap.LatLng(that.path. [0]. [0], that.path. [0]. [1]))
						/*author:李浩杨
						data:2021/7/13
						实现逆地址解析并将结果显示到信息窗口来显示,使用get请求来实现逆地址解析*/
						for (var i = 0; i < that.marked.location.length; i++) {
							// 遍历marked的数组来加点
							that.markerLayer.add({
								"id": i, //点标记唯一标识
								"styleId": 'myStyle',
								"position": new TMap.LatLng(that.marked.location[i]. ["lat"], that.marked
									.location[i]. ["lng"]),
								"properties": {
									"name": res.data.name,
									"Time": that.marked.location[i].time
								}
							}), error => {
								console.log("错误")
							}
						}
						//轨迹图
						var trail = new TMap.visualization.Trail({
								map: that.Map,
								pickStyle: function(item) { //轨迹图样式映射函数
									return {
										width: 10,
										color: "#00FFFF",
									}
								},
								startTime: 0, //动画循环周期的起始时间戳
								showDuration: 120, //动画中轨迹点高亮的持续时间
								playRate: 1000 // 动画播放倍速
							})
							.addTo(that.Map)
							.setData([{
								path: that.path
							}])
						/*author:孙玉斐
						data:2021/7/15
						引用折线构造函数，定义折线样式*/
						that.polylineLayer = new TMap.MultiPolyline({
							id: 'polyline-layer', //图层唯一标识
							map: that.Map, //设置折线图层显示到哪个地图实例中
							//折线样式定义
							styles: {
								'style_red': new TMap.PolylineStyle({
									'color': '#CC0000', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								}),
								'style_green': new TMap.PolylineStyle({
									'color': '#00FF00', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								})
							}
						});
						that.otherpolylineLayer = new TMap.MultiPolyline({
							id: 'otherpolyline-layer', //图层唯一标识
							map: that.Map, //设置折线图层显示到哪个地图实例中
							//折线样式定义
							styles: {
								'style_red': new TMap.PolylineStyle({
									'color': '#CC0000', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								}),
								'style_green': new TMap.PolylineStyle({
									'color': '#00FF00', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								})
							}
						});

						var polyArray = new Array();
						var i = 0;
						for (var item in that.tril) {
							polyArray[i] = [that.tril[i].lat, that.tril[i].lng],
								i = i + 1
						}
						//转为LatLng数组
						polyArray = polyArray.map(p => {
							return new TMap.LatLng(p[0], p[1]);
						});
						that.polylineLayer.add({ //新的折线添加到图层中
							'id': 'pl_1',
							'styleId': 'style_red',
							'paths': polyArray
						})
					});

				//实现地图上标记和点标记显示信息窗口的功能
				that.markerLayer = new TMap.MultiMarker({
					map: that.Map, //指定地图容器
					//标记样式自定义
					styles: {
						"myStyle": new TMap.MarkerStyle({
							"width": 25,
							"height": 35,
							"src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png",
							"anchor": {
								x: 16,
								y: 32
							}
						})
					},
				});
				that.othermarkerLayer = new TMap.MultiMarker({
					map: that.Map, //指定地图容器
					//标记样式自定义
					styles: {
						"myStyle": new TMap.MarkerStyle({
							"width": 25,
							"height": 35,
							// "src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png",
							"anchor": {
								x: 16,
								y: 32
							}
						})
					},
				});
				//初始化信息窗口
				var infoWindow = new TMap.InfoWindow({
					map: that.Map,
					position: new TMap.LatLng(30.5263, 114.348),
					offset: {
						x: 0,
						y: -32
					},
					enableCustome: true,
				});
				infoWindow.close();
				//标记点击事件，显示区域位置信息
				var clickHandler = function(evt) {
					infoWindow.open();
					infoWindow.setPosition(evt.geometry.position);
					console.log(evt)
					that.$axios
						.post('https://120.25.76.71/api/content', {
							url: "https://apis.map.qq.com/ws/geocoder/v1/?location=" + evt.geometry.position.lat +
								"," + evt.geometry.position.lng + "&" + "key=EHABZ-5AFLU-T5XV6-BHFBR-JRKTZ-2UFJ6"
						})
						.then(res => {
							// 遍历marked的数组来加点
							console.log(res.data)
							infoWindow.setContent('<div><div class="divbox">' + evt.geometry.properties.name +
								'</div><div calss="divtextbox">' + res
								.data + '<br>' + evt.geometry.properties.Time + '</div></div>');
						})
				}
				// var changePeople = function(evt){
				// 	initMap(evt.geometry.properties.name,evt.geometry.properties.identy,[that.marked[evt.geometry.properties.name].[0].firTime,that.marked[evt.geometry.properties.name].[(evt.geometry.properties.name.length-1)].lasTime])
				// }
				that.markerLayer.on("click", clickHandler)
				that.othermarkerLayer.on("click", clickHandler)
				console.log(that.Map)
				console.log(window)
			},
		},
		//用钩子函数mounted在页面加载时显示地图与标记
		mounted() {
			this.initMap(this.$route.query.name, this.$route.query.identy, this.$route.query.time)
		}
	}
</script>

<style>
	.box {
		width: 100%;
		height: 100%;
	}

	.box #container {
		/*地图(容器)显示大小*/
		/*设置为全屏，位于最下层*/
		width: 100%;
		height: 100%;
		z-index: 1;
	}

	.box .info {
		position: absolute;
		left: 200px;
		top: 60px;
		z-index: 999;
	}

	.box .info .button {
		width: 90px;
		height: 45px;
		font-size: 15px;
		position: absolute;
		left: 20px;
		top: 10px;
		border: none;
		background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
		z-index: 1000;
	}

	.box .word1 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
		top: 90px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word1 .line1 {
		width: 50px;
		height: 5px;
		background-color: red;
	}

	.box .word2 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
		top: 150px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word2 .line2 {
		width: 60px;
		height: 5px;
		background-color: greenyellow;
	}

	.divbox {
		background-color: #333333;
	}

	.divtextbox {
		background-color: #333333;
	}
</style>
