<template>
	<div class="background" style="overflow:-Scroll;overflow-y:hidden">
		<video :style="fixStyle" autoplay loop muted class="login-fillWidth" v-on:canplay="canplay">
			<source src="../video/login.mp4" type="video/mp4" />
		</video>
		<div class="login_box">
			<!-- 头像区域 -->
			<div class="avatar_box1">
				<img src="../image/logo1.png" />
			</div>
			<div class="avatar_box2">
				<img src="../image/Login.png" />
			</div>
			<!-- 登陆表单 -->
			<el-form ref="loginFormRef" class="login_form" :model="loginForm" :rules="loginFormRules">
				<!-- 用户名 -->
				<el-form-item prop="username">
					<div class="inputText">
						<el-input v-model="loginForm.username" prefix-icon="el-icon-user"
							placeholder="Username"></el-input>
					</div>
				</el-form-item>
				<!-- 密码 -->
				<el-form-item prop="password">
					<div class="inputText" style="margin-top: -30px;">
						<el-input type="password" v-model="loginForm.password" prefix-icon="el-icon-lock"
							placeholder="Password"></el-input>
					</div>
				</el-form-item>
				<!-- 登陆按钮 -->
				<el-button type="LOGIN" round class="inputButton" @click="login()">登 陆</el-button>
				<!-- 注册 -->
				<div id="signIn" @click="register()">
					<el-link type="primary">
						没有账号？点击注册
					</el-link>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'Login',
		data() {
			// var name = (rule, value, callback) => {
			// 	if (value === '') {
			// 		callback(new Error('请输入用户名'));
			// 	} else {
			// 		if (this.ruleForm.checkPass !== '') {
			// 			this.$refs.ruleForm.validateField('checkPass');
			// 		}
			// 		callback();
			// 	}
			// };
			return {
				videoCanPlay: false,
				fixStyle: '',
				/* author:孙玉斐
				date:7.7
				登陆表单信息*/
				loginForm: {
					username: '',
					password: '',
				},

				/* author:孙玉斐
				date:7.7
				登陆表单验证信息*/
				loginFormRules: {
					// 验证用户名是否合法
					username: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入用户名',
							trigger: 'blur'
						},
						{
							min: 4,
							max: 10,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0长度在 4 到 10 个字符',
							trigger: 'blur'
						},
						{
							pattern: /^[a-zA-Z]+$/,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0只能输入4-10位的字母',
							trigger: 'blur'
						}
					],
					// 验证密码是否合法
					password: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入密码',
							trigger: 'blur'
						},
						{
							min: 6,
							max: 16,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0长度在 6 到 16 个字符',
							trigger: 'blur'
						},
						{
							pattern: /^(\w){6,16}$/,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0只能输入6-16个字母、数字、下划线',
							trigger: 'blur'
						},
					]
				}
			}
		},
		methods: {
			/* author:孙玉斐
			date:7.7
			登陆界面的登陆函数，提交登陆表单信息并跳转*/
			login() {
				this.$refs.loginFormRef.validate(async valid => {
					if (!valid) {
						this.$refs.loginFormRef.resetFields()
						this.$message.error('登陆失败');
					}
					let formData = new FormData();
					for (let key in this.loginForm) {
						formData.append(key, this.loginForm[key]);
						console.log(formData.get(key));
					}

					this.$axios
						.post('https://120.25.76.71/api/login', {
							"username": this.loginForm.username,
							"password": this.loginForm.password
						})
						.then(response => {
							this.$message.success('登陆成功')
							this.$router.push('./home');
						}, error => {
							this.$message.error('登陆失败')
						})
				});
			},
			/* author:孙玉斐
			date:7.7
			登陆界面的跳转函数，跳转到注册界面*/
			register() {
				this.$router.push('./register');
			},
			/* author:孙玉斐
			date:7.7
			登陆界面的跳转函数，跳转到主界面*/
			home() {
				this.$router.push('./home');
			},
			/* author:孙玉斐
			date:7.14
			视频显示函数，将控制视频显示参数置位true*/
			canplay() {
				this.videoCanPlay = true
			},
		},
		/* author:孙玉斐
		date:7.14
		采用vue钩子函数mounted，当页面加载完成事使视频背景与整个页面同步大小*/
		mounted: function() {
			//主要是检测窗口缩放视频也同步大小
			window.onresize = () => {
				const windowWidth = document.body.clientWidth
				const windowHeight = document.body.clientHeight
				const windowAspectRatio = windowHeight / windowWidth
				let videoWidth
				let videoHeight
				if (windowAspectRatio < 0.5625) {
					videoWidth = windowWidth
					videoHeight = videoWidth * 0.5625
					this.fixStyle = {
						height: windowWidth * 0.5625 + 'px',
						width: windowWidth + 'px',
						'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
						'margin-left': 'initial'
					}
				} else {
					videoHeight = windowHeight
					videoWidth = videoHeight / 0.5625
					this.fixStyle = {
						height: windowHeight + 'px',
						width: windowHeight / 0.5625 + 'px',
						'margin-left': (windowWidth - videoWidth) / 2 + 'px',
						'margin-bottom': 'initial'
					}
				}
			}
			window.onresize();
		}
	};
</script>

<style Lang="less" scoped="scoped">
	.background {
		/**宽高100%是为了图片铺满屏幕 */
		z-index: 1;
		position: absolute;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.background video {
		z-index: -1;
		object-fit:fill;
		height: 100%;
		width: 100%;
		position: absolute;
		right: 0;
		bottom: 0;
	}
	
	.login_box {
		margin: 0 auto;
		margin-top: 12%;
		padding: 20px 50px;
		background-color: #00000060;
		width: 600px;
		height: 400px;
		border-radius: 10px;
		text-align: center;
	}
	
	.login_box .avatar_box1 {
		height: 80px;
		width: 200px;
		position: absolute;
		left: 37%;
		transform: translate(-55%);
	}
	
	.login_box .avatar_box1 img {
		height: 65px; 
		width: 160px;
	}

	.login_box .avatar_box2 {
		height: 100px;
		width: 100px;
		border: 1px solid #eee;
		border-radius: 50%;
		box-shadow: 0 0 10px #AAAAAA;
		position: absolute;
		left: 50%;
		transform: translate(-50%);
	}

	.login_box .avatar_box2 img {
		height: 100%;
		width: 100%;
		border-radius: 50%;
	}

	.login_box .login_form {
		margin-top: 50px;
	}

	.login_box .login_form .inputText {
		width: 300px;
		margin-left: 150px;
		padding-top: 70px;
	}

	.login_box .login_form .inputText i {
		color: white;
	}

	.login_box .login_form .inputText input {
		border: 0;
		width: 40%;
		font-size: 15px;
		color: #FFFFFF;
		background: #FFFFFF90;
		border-bottom: 2px solid #FFFFFF;
		padding: 5px 10px;
		margin-top: 10px;
	}

	.login_box .inputButton {
		border: 0px;
		width: 100px;
		height: 35px;
		color: white;
		margin-top: 20px;
		border-radius: 20px;
		background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
	}

	.login_box #signIn {
		margin: 4% auto;
	}
</style>
