<template>
	<div class="search" style="overflow:-Scroll;overflow-y:hidden;overflow-x:hidden">
		<!-- 视频背景 -->
		<div class="container" :style="contain">
			<video autoplay loop muted class="login-fillWidth" v-on:canplay="canplay">
				<source src="../../video/login.mp4" type="video/mp4" />
			</video>
		</div>
		<!-- 位置坐标动画 -->
		<div class="loc">
			<lottie-player src="https://assets8.lottiefiles.com/packages/lf20_1fkkgt7r.json" background="transparent"
				speed="1" loop autoplay></lottie-player>
		</div>
		<!-- 查询表单 -->
		<el-form ref="SearchFormRef" :model="SearchForm" label-width="80px" class="searchForm" :rules="SearchFormRules"
			status-icon>
			<div class="font">
				<h1>查 询 患 者 信 息</h1>
			</div>
			<!-- 姓名输入 -->
			<div class="inputText">
				<el-form-item label="姓名" class="label" prop="name">
					<el-input v-model="SearchForm.name" prefix-icon="el-icon-user-solid"></el-input>
				</el-form-item>
			</div>
			<!-- 身份证号输入 -->
			<div class="inputText" style="margin-top: 40px;">
				<el-form-item label="身份证号" class="label" prop="identy">
					<el-input v-model="SearchForm.identy" prefix-icon="el-icon-postcard"></el-input>
				</el-form-item>
			</div>
			<!-- 查询日期 -->
			<div class="inputText" style="margin-top: 40px;">
				<el-form-item label="查询日期" class="label" prop="time">
					<el-col :span="11">
						<el-date-picker v-model="SearchForm.time" type="datetimerange" :picker-options="pickerOptions"
							range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right"
							value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm:ss">
						</el-date-picker>
					</el-col>
				</el-form-item>
			</div>
			<!-- 按钮 -->
			<el-form-item>
				<el-button type="primary" round class="button1" @click="onload()">查 询</el-button>
				<el-button class="button2" round @click="cancel()" style="margin-left: 90px">取 消</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		name: 'Search',
		data() {
			return {
				/* author:孙玉斐
				date:7.12
				elementui中的日期时间选择器的运行函数*/
				pickerOptions: {
					shortcuts: [{
						text: '最近一周',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近一个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近三个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				videoCanPlay: false,
				fixStyle: '',
				contain: {
					height: "",
					width: "",
				},
				/* author:孙玉斐
				date:7.12
				查询信息提交表单*/
				SearchForm: {
					name: '',
					identy: '',
					time: [new Date(2021, 6, 23, 0, 0), new Date(2021, 7, 1, 0, 0)],
				},

				/* author:孙玉斐
				date:7.12
				查询信息提交表单的验证规则*/
				SearchFormRules: {
					name: [{
							required: true,
							message: '请输入姓名',
							trigger: 'blur'
						},
						{
							pattern: '^[\\u4e00-\\u9fa5]{2,4}$',
							message: '请输入正确的姓名',
							trigger: 'blur'
						}
					],
					identy: [{
							required: true,
							message: "请输入身份证号",
							trigger: "blur"
						},
						{
							pattern: '^[1-9]\\d{5}(18|19|20|(3\\d))\\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$',
							message: '请输入正确的身份证号',
							trigger: 'blur'
						}
					],
				},

			}
		},
		methods: {
			/* author:孙玉斐
			date:7.12
			用axios中的post方法向服务器发送表单信息并接收*/
			onload() {
				this.$refs.SearchFormRef.validate(async valid => {
					if (!valid) {
						this.$refs.SearchFormRef.resetFields()
						this.$notify.error({
							title: '错误',
							message: '输入的信息不符合规则'
						});
					}
					let formData = new FormData();
					for (let key in this.SearchForm) {
						formData.append(key, this.SearchForm[key]);
						console.log(formData.get(key));
					}

					this.$confirm('是否确认查询?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.$router.push({
							path: "/map",
							name: "Map",
							query: {
								name: this.SearchForm.name,
								identy: this.SearchForm.identy,
								time: this.SearchForm.time,
							},
							params: {
								usersitelist: 'userlist'
							}
						});
						this.$axios
							.post('https://120.25.76.71/api/func', {
								"name": this.SearchForm.name,
								"identy": this.SearchForm.identy,
								"time": this.SearchForm.time
							})
							.then(response => {
								this.$message.success('成功查询患者')
								this.$router.push('./home');
							}, error => {
								this.$message.error('查询失败')
							})
					}).catch(() => {});
				});
			},

			/* author:孙玉斐
			date:7.12
			取消按钮清空所填信息*/
			cancel() {
				this.$confirm('是否确认要取消查询?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$refs.SearchFormRef.resetFields()
				}).catch(() => {});
			},
			/* author:孙玉斐
			date:7.15
			视频显示函数，将控制视频显示参数置位true*/
			canplay() {
				this.videoCanPlay = true
			},
			
			getDefault() {
				this.contain.height = window.innerHeight - 62 + "px";
				this.contain.width = window.innerWidth - 201 + "px";
			},
		},
		created() {
			//页面创建时执行一次getDefault进行赋值，顺道绑定resize事件
			window.addEventListener("resize", this.getDefault);
			this.getDefault();
		},
	}
</script>

<style>	
	.search .container{
		position: absolute;
		z-index: -1;
		margin: 0;
		padding: 0;
	}

	.el-form {
		margin-top: 13%;
	}

	.label .el-form-item__label {
		color: #fff;
		font-size: 13px;
	}

	.searchForm .inputText {
		width: 480px;
		margin-left: 31%;
		margin-top: 18%;
	}

	.searchForm .button1 {
		margin-left: -12%;
		width: 90px;
		height: 45px;
		margin-top: 30px;
		font-size: 17px;
		border: none;
		color: #111;
		background-image: linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%);
	}

	.searchForm .button2 {
		margin-left: -12%;
		width: 90px;
		height: 45px;
		margin-top: 30px;
		font-size: 17px;
		border: none;
		color: #111;
		background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
	}

	.searchForm .font {
		margin-left: 32%;
		margin-top: 8%;
		font: bold 50px arial, sans-serif;
		font-family: "宋体";
	}

	.loc lottie-player {
		width: 150px;
		height: 150px;
		position: absolute;
		right: 20px;
		z-index: -1;
	}
</style>
