<template>
	<div class="background" style="overflow:-Scroll;overflow-y:hidden">
		<video :style="fixStyle" autoplay loop muted class="login-fillWidth" v-on:canplay="canplay">
			<source src="../video/login.mp4" type="video/mp4" />
		</video>
		<div class="register_box">
			<!-- 头像区域 -->
			<div class="avatar_box1">
				<img src="../image/logo1.png" />
			</div>
			<div class="avatar_box2">
				<img src="../image/Login.png" alt="">
			</div>
			<!-- 登陆表单 -->
			<el-form ref="registerFormRef" class="register_form" :model="registerForm" :rules="registerFormRules"
				status-icon>
				<!-- 用户名 -->
				<el-form-item prop="username">
					<div class="inputText1">
						<el-input prefix-icon="el-icon-user" v-model="registerForm.username" placeholder="Username">
						</el-input>
					</div>
				</el-form-item>
				<!-- 手机号验证 -->
				<el-form-item prop="phone">
					<div class="inputText2">
						<el-input prefix-icon="el-icon-phone" v-model="registerForm.phone" placeholder="Phone Number">
						</el-input>
					</div>
				</el-form-item>
				<!-- 密码 -->
				<el-form-item prop="password">
					<div class="inputText3">
						<el-input type="password" v-model="registerForm.password" prefix-icon="el-icon-lock"
							placeholder="Password" autocomplete="off"></el-input>
					</div>
				</el-form-item>
				<!-- 确认密码 -->
				<el-form-item prop="checkPass">
					<div class="inputText4">
						<el-input type="password" v-model="registerForm.checkPass" prefix-icon="el-icon-lock"
							placeholder="CheckPass" autocomplete="off"></el-input>
					</div>
				</el-form-item>
				<!-- 邀请码 -->
				<el-form-item prop="code">
					<div class="inputText5">
						<el-input prefix-icon="el-icon-eleme" v-model="registerForm.code" placeholder="Invitation Code">
						</el-input>
					</div>
				</el-form-item>
				<el-button type="LOGIN" round class="inputButton" @click="register()">注 册</el-button>
				<div id="signIn" @click="login()">
					<el-link type="primary">已有账号？点击登陆</el-link>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Register',
		data() {
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入密码'));
				} else {
					if (this.registerForm.checkPass !== '') {
						this.$refs.registerFormRef.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请再次输入密码'));
				} else if (value !== this.registerForm.password) {
					callback(new Error('\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0两次输入密码不一致!'));
				} else {
					callback();
				}
			};
			var checkphone = (rule, value, callback) => {
				// let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
				if (value === "") {
					callback(new Error("\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入手机号"));
				} else if (!this.isCellPhone(value)) {
					// 引入methods中封装的检查手机格式的方法
					callback(new Error("\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入正确的手机号!"));
				} else {
					callback();
				}
			};
			return {
				videoCanPlay: false,
				fixStyle: '',
				/* author:孙玉斐
				date:7.8
				注册表单信息*/
				registerForm: {
					username: '',
					password: '',
					checkPass: '',
					phone: '',
					code: ''
				},
				/* author:孙玉斐
				date:7.8
				注册表单验证信息*/
				registerFormRules: {
					// 验证用户名是否合法
					username: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入用户名',
							trigger: 'blur'
						},
						{
							min: 4,
							max: 10,
							message: '长度在 4 到 10 个字符',
							trigger: 'blur'
						}, 
						{
							pattern: /^[a-zA-Z]+$/,
							message: '只能输入4-10位的字母',
							trigger: 'blur'
						}
					],
					// 验证密码是否合法
					password: [
						// { required: true, message: '请输入密码', trigger: 'blur' },
						// { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' },
						{
							validator: validatePass,
							trigger: 'blur'
						},
						{
							min: 6,
							max: 16,
							message: '长度在 6 到 16个字符',
							trigger: 'blur'
						},
						{
							pattern: /^(\w){6,16}$/,
							message: '只能输入6-16个字母、数字、下划线',
							trigger: 'blur'
						},
					],
					// 验证确认密码是否合法
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}],
					phone: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入手机号码',
							trigger: 'blur'
						},
						{
							validator: checkphone,
							trigger: 'blur'
						}
					]
				}
			}
		},
		methods: {
			/* author:孙玉斐
			date:7.8
			验证手机号是否正确*/
			isCellPhone(val) {
				if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
					return false;
				} else {
					return true;
				}
			},
			/* author:孙玉斐
			date:7.8
			注册界面的注册函数，验证表单信息并提交给服务器，跳转到登录页面*/
			register() {
				this.$refs.registerFormRef.validate(async valid => {
					if (!valid) {
						this.$refs.registerFormRef.resetFields()
						this.$message.error('注册失败');
					}
					let formData = new FormData();
					for (let key in this.registerForm) {
						formData.append(key, this.registerForm[key]);
						console.log(formData.get(key));
					}

					this.$axios
						.post('https://120.25.76.71/api/register', {
							"username": this.registerForm.username,
							"phone": this.registerForm.phone,
							"password": this.registerForm.password
						})
						.then(response => {
							this.$message.success('注册成功')
							this.$router.push('./login');
						}, error => {
							this.$message.error('注册失败')
						})
				});
			},
			/* author:孙玉斐
			date:7.8
			注册界面的跳转函数，跳转到登陆界面*/
			login() {
				this.$router.push('./login');
			},
			// toLogin() {
			// 	this.$router.replace('/login');
			// }
			/* author:孙玉斐
			date:7.14
			视频显示函数，将控制视频显示参数置位true*/
			canplay() {
				this.videoCanPlay = true
			},
		},
		/* author:孙玉斐
		date:7.14
		采用vue钩子函数mounted，当页面加载完成事使视频背景与整个页面同步大小*/
		mounted: function() {
			//主要是检测窗口缩放视频也同步大小
			window.onresize = () => {
				const windowWidth = document.body.clientWidth
				const windowHeight = document.body.clientHeight
				const windowAspectRatio = windowHeight / windowWidth
				let videoWidth
				let videoHeight
				if (windowAspectRatio < 0.5625) {
					videoWidth = windowWidth
					videoHeight = videoWidth * 0.5625
					this.fixStyle = {
						height: windowWidth * 0.5625 + 'px',
						width: windowWidth + 'px',
						'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
						'margin-left': 'initial'
					}
				} else {
					videoHeight = windowHeight
					videoWidth = videoHeight / 0.5625
					this.fixStyle = {
						height: windowHeight + 'px',
						width: windowHeight / 0.5625 + 'px',
						'margin-left': (windowWidth - videoWidth) / 2 + 'px',
						'margin-bottom': 'initial'
					}
				}
			}
			window.onresize();
		}
	};
</script>

<style Lang="less" scoped="scoped">
	.background {
		/**宽高100%是为了图片铺满屏幕 */
		z-index: 1;
		position: absolute;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.background video {
		z-index: -1;
		object-fit: fill;
		height: 100%;
		width: 100%;
		position: absolute;
		right: 0;
		bottom: 0;
	}

	.register_box {
		margin: 0 auto;
		margin-top: 6%;
		padding: 20px 50px;
		background-color: #00000090;
		width: 400px;
		height: 600px;
		border-radius: 10px;
		text-align: center;
	}

	.register_box .avatar_box1 {
		height: 80px;
		width: 200px;
		position: absolute;
		left: 42%;
		transform: translate(-55%);
	}

	.register_box .avatar_box1 img {
		height: 65px;
		width: 160px;
	}

	.register_box .avatar_box2 {
		height: 100px;
		width: 100px;
		border: 1px solid #eee;
		border-radius: 50%;
		box-shadow: 0 0 10px #AAAAAA;
		position: absolute;
		left: 50%;
		transform: translate(-50%);
	}

	.register_box .avatar_box2 img {
		height: 100%;
		width: 100%;
		border-radius: 50%;
	}

	.register_box .register_form {
		margin-top: 50px;
	}

	.register_box .register_form .inputText1 {
		width: 300px;
		margin-left: 50px;
		padding-top: 70px;
	}
	.register_box .register_form .inputText2 {
		width: 300px;
		margin-left: 50px;
		padding-top: 20px;
	}
	.register_box .register_form .inputText3 {
		width: 300px;
		margin-left: 50px;
		padding-top: 20px;
	}
	.register_box .register_form .inputText4 {
		width: 300px;
		margin-left: 50px;
		padding-top: 20px;
	}
	.register_box .register_form .inputText5 {
		width: 300px;
		margin-left: 50px;
		padding-top: 20px;
	}

	.register_box .register_form .inputText i {
		color: white;
	}

	.register_box .register_form .inputText input {
		border: 0;
		width: 40%;
		font-size: 15px;
		color: #FFFFFF;
		background: #FFFFFF90;
		border-bottom: 2px solid #FFFFFF;
		padding: 5px 10px;
		margin-top: 10px;
	}

	.register_box .inputButton {
		border: 0px;
		width: 100px;
		height: 35px;
		color: white;
		margin-top: 20px;
		border-radius: 20px;
		background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
	}

	.register_box #signIn {
		margin: 5% auto;
	}
</style>
